"use client";

import { useState } from "react";
import { TContactForm } from "@/types";
import { api } from "@/data/data-api";
import { Button } from "@/components/ui/button";
import {
  Field,
  FieldGroup,
  FieldLabel,
} from "@/components/ui/field";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";

export interface IContactFormLabelsProps {
  id: number;
  __component: string;
  heading: string;
  subHeading: string;
  name: string;
  email: string;
  phone: string;
  location: string;
  instagram: string;
  experience: string;
  interest: string;
  clients: string;
  info: string;
}

type FormData = {
  name: string;
  email: string;
  phone: string;
  location: string;
  instagram: string;
  experience: string;
  interest: string;
  clients: string;
  info: string;
};

export function ContactFormLabels({ data }: { data: IContactFormLabelsProps }) {
  const [formData, setFormData] = useState<FormData>({
    name: "",
    email: "",
    phone: "",
    location: "",
    instagram: "",
    experience: "",
    interest: "",
    clients: "",
    info: "",
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitStatus, setSubmitStatus] = useState<{
    type: "success" | "error" | null;
    message: string;
  }>({ type: null, message: "" });

  if (!data) return null;

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setFormData((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    setSubmitStatus({ type: null, message: "" });

    try {
      const payload = {
        data: {
          name: formData.name,
          email: formData.email,
          phone: formData.phone,
          location: formData.location,
          instagram: formData.instagram || undefined,
          experience: formData.experience,
          interest: formData.interest,
          clients: formData.clients ? parseInt(formData.clients) : undefined,
          info: formData.info || undefined,
        },
      };

      const response = await api.post<TContactForm>(
        `${process.env.NEXT_PUBLIC_STRAPI_URL}/api/contact-forms`,
        payload
      );

      if (response.success) {
        setSubmitStatus({
          type: "success",
          message: "Thank you! Your submission has been received.",
        });
        setFormData({
          name: "",
          email: "",
          phone: "",
          location: "",
          instagram: "",
          experience: "",
          interest: "",
          clients: "",
          info: "",
        });
      } else {
        setSubmitStatus({
          type: "error",
          message:
            response.error?.message || "Something went wrong. Please try again.",
        });
      }
    } catch {
      setSubmitStatus({
        type: "error",
        message: "Failed to submit form. Please try again.",
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="w-full max-w-4xl mx-auto px-4 py-12">
      <div className="mb-8 text-center">
        <h2 className="text-3xl font-bold mb-2">{data.heading}</h2>
        <p className="text-muted-foreground">{data.subHeading}</p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-6">
        <FieldGroup className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {/* Name */}
          <Field>
            <FieldLabel htmlFor="name">{data.name}</FieldLabel>
            <input
              id="name"
              name="name"
              type="text"
              required
              value={formData.name}
              onChange={handleChange}
              className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
            />
          </Field>

          {/* Email */}
          <Field>
            <FieldLabel htmlFor="email">{data.email}</FieldLabel>
            <input
              id="email"
              name="email"
              type="email"
              required
              value={formData.email}
              onChange={handleChange}
              className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
            />
          </Field>

          {/* Phone */}
          <Field>
            <FieldLabel htmlFor="phone">{data.phone}</FieldLabel>
            <input
              id="phone"
              name="phone"
              type="tel"
              required
              value={formData.phone}
              onChange={handleChange}
              className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
            />
          </Field>

          {/* Location */}
          <Field>
            <FieldLabel htmlFor="location">
              {data.location}
            </FieldLabel>
            <input
              id="location"
              name="location"
              type="text"
              required
              value={formData.location}
              onChange={handleChange}
              className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
            />
          </Field>

          {/* Instagram */}
          <Field>
            <FieldLabel htmlFor="instagram">
              {data.instagram}
            </FieldLabel>
            <input
              id="instagram"
              name="instagram"
              type="text"
              value={formData.instagram}
              onChange={handleChange}
              className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
            />
          </Field>

          {/* Experience */}
          <Field>
            <FieldLabel htmlFor="experience">
              {data.experience}
            </FieldLabel>
            <input
              id="experience"
              name="experience"
              type="text"
              required
              value={formData.experience}
              onChange={handleChange}
              className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
            />
          </Field>

          {/* Interest */}
          <Field>
            <FieldLabel htmlFor="interest">
              {data.interest}
            </FieldLabel>
            <input
              id="interest"
              name="interest"
              type="text"
              required
              value={formData.interest}
              onChange={handleChange}
              className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
            />
          </Field>

          {/* Clients */}
          <Field>
            <FieldLabel htmlFor="clients">
              {data.clients}
            </FieldLabel>
            <input
              id="clients"
              name="clients"
              type="number"
              value={formData.clients}
              onChange={handleChange}
              className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
            />
          </Field>
        </FieldGroup>

        {/* Info - Full Width */}
        <Field>
          <FieldLabel htmlFor="info">{data.info}</FieldLabel>
          <textarea
            id="info"
            name="info"
            rows={4}
            value={formData.info}
            onChange={handleChange}
            className="flex min-h-20 w-full rounded-md border border-input bg-transparent px-3 py-2 text-base shadow-sm placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
          />
        </Field>

        {/* Submit Status */}
        {submitStatus.type && (
          <div
            className={`p-4 rounded-md ${
              submitStatus.type === "success"
                ? "bg-green-50 text-green-800 dark:bg-green-900/20 dark:text-green-400"
                : "bg-red-50 text-red-800 dark:bg-red-900/20 dark:text-red-400"
            }`}
          >
            {submitStatus.message}
          </div>
        )}

        {/* Submit Button */}
        <Button type="submit" disabled={isSubmitting} className="w-full">
          {isSubmitting ? "Submitting..." : "Submit"}
        </Button>
      </form>
    </div>
  );
}